import { useState } from "react"
import { Link } from "react-router-dom";
import { useQuery } from "react-query";
import styled from "styled-components";

import { fetchCoins } from "../api";

// Reset CSS : https://github.com/zacanger/styled-reset/blob/master/src/index.ts

//      
const Container = styled.div`
    padding: 0px 20px;
    max-width: 80%;
    margin: 0 auto;
`;

const SearchBox = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: bisque;
    height: 80px;
    margin-bottom: 50px;
    border-radius: 15px;
    box-shadow: 2px 3px 5px 0px;
`

const TextBox = styled.span`
    display: flex;
    justify-content: center;
    align-items: center;
    
    height: 40px;
    margin-right: 20px;
    font-weight: bold;
    color:black;
`
const SearchBar = styled.div`
    position: relative;
`

const SearchInput = styled.input.attrs({placeholder: "typing coin name"})`
    border-radius: 5px;
    padding: 5px 10px;
    width: 400px;
    height: 40px;
`

const SearchResult = styled.div`
    position: absolute;
    top: 40px;
    left: 0px;
    width: 400px;
    max-height: 500px;
    overflow: scroll;
    background-color: white;
    padding: 0px 10px;
`

const SearchList = styled.ul`
    width: 100%;
    
`
const SearchItems = styled.li`
    display: flex;
    justify-content: left;
    align-items: center;
    height: 60px;
    padding: 0px 20px;
    color: black;
    &:hover {
        background-color: azure;
    }
    &:active {
        background-color: blanchedalmond;
    }
`

const CoinsList = styled.ul`
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
`;

const Coin = styled.li`
    background-color: white;
    color: black;
    width: 100%;
    padding: 20px;
    border-radius: 15px;
    a {
        display: flex;
        align-items: center;
        padding: 20px;
        transition : color 0.2s ease-in;
    }
    &:hover {
        a{
            color: ${props => props.theme.accentColor};
        }
    }
`;

const Loader = styled.span`
    display: block;
    text-align: center;
    font-size: 30px;
`;

const CoinLogo = styled.img`
    height:25px;
    aspect-ratio: 1/1;
    margin-right: 20px;
`;

const CoinName = styled.span`
    display: flex;
    justify-content: left;
    align-items: center;
    font-size: 14px;
`

interface CoinInterface {
    id: string,
    name: string,
    symbol: string,
    rank: number,
    is_new: boolean,
    is_active: boolean,
    type: string,
};

// ** Link component   option!
// Link to={} state={}
//  Link state     Link state       .
function CoinTrackerHome() {
    
    const [searchCoin , setSearchCoin] = useState("");
    //   : query  
    //   : fetcher 
    //  : isLoading (boolean) :   fetcher   
    // 2 : fetcher  return  
    // ++    useQuery   ()
    const {isLoading, data} = useQuery<CoinInterface[]>("allCoins", fetchCoins);

    /*     
    [ react-query     ]
    reacut query       

    const [loading, setLoading] = useState<boolean>(true);
    const [coins, setCoins] = useState<CoinInterface[]>([]);
    
    const getCoinApi = async() => {
        const response = await axios.get("https://api.coinpaprika.com/v1/coins")
        setCoins(response.data.slice(0,100));
        setLoading(false);
    }
    useEffect(()=>{
        getCoinApi();
    }, []) */;

    return (
        <Container>
            <SearchBox>
                <TextBox>Coin Search</TextBox>
                <SearchBar>
                    <SearchInput value={searchCoin} onChange={(event)=>setSearchCoin(event.target.value)} />
                    {
                        searchCoin ? 
                        <SearchResult>
                            {data?.filter((item)=> item.name.toLowerCase().includes(searchCoin)).map((coin)=>(
                            <SearchList>
                                <Link to={`/coin-tracker/${coin.id}`} state={coin.name}>
                                    <SearchItems>
                                        <CoinLogo src={`https://coinicons-api.vercel.app/api/icon/${coin.symbol.toLowerCase()}`}></CoinLogo>
                                        <CoinName>{coin.name}</CoinName>
                                    </SearchItems>
                                </Link>
                            </SearchList>
                            ))}
                            
                        </SearchResult>
                        :
                        null
                    }
                </SearchBar>
            </SearchBox>
            { isLoading ? (
                <Loader>"Loading..."</Loader>
            ):(
            <CoinsList>
                {data?.map((coin)=>(
                <Coin key={coin.id}>
                    <Link to={`/coin-tracker/${coin.id}`} state={coin.name}>
                        <CoinLogo src={`https://coinicons-api.vercel.app/api/icon/${coin.symbol.toLowerCase()}`} />
                        {coin.name} &rarr;
                    </Link>
                </Coin>))}
                
            </CoinsList>
            )}
        </Container>
    )
}
export default CoinTrackerHome;