import { Link, useSearchParams } from "react-router-dom";
import { users } from "../db";

function UserHome() {
    const [ readSearchParams, setSearchParams ] = useSearchParams(); // url  query string  
    console.log(readSearchParams);

    return (
        <div>
            <h1>Users</h1>
            <ul>
                {users.map((user) => (
                    <li key={user.id}>
                        <Link to={`/users/${user.id}`}>{user.name}</Link>
                    </li>
                ))}
            </ul>
        </div>
    );
}

// useSearchParams  URLSearchParams  (JS)

export default UserHome;