
import { createBrowserRouter } from "react-router-dom";
import StyledComponentPart from "./Styled Component/part_4";
import TypescriptPart from "./Typescript/part2-4";
import App from "./App";
import NotFound from "./NotFound";
import UserHome from "./screens/UserHome";
import User from "./screens/users/Users";
import Followers from "./screens/users/Followers";
import CoinTrackerHome from "./CoinTracker/CoinTrackerHome";
import Coin from "./CoinTracker/Coin";
import Price from "./CoinTracker/Price";
import Chart from "./CoinTracker/Chart";

// router  array     .
const router = createBrowserRouter([
    {
        //  router  root
        path: "/",
        element: <App />,
        children: [
            {
                path:"styled-component",
                element: <StyledComponentPart />,
            },
            {
                path:"typescript",
                element: <TypescriptPart />,
            }
            ,
            {
                path: "users",
                element: <UserHome />
            }
            ,
            {
                path: "users/:userId",
                element: <User />,
                children: [
                    {
                        path: "followers",
                        element: <Followers />
                    }
                ]
            }
            ,
            {
                path: "coin-tracker",
                element: <CoinTrackerHome />,
            }
            ,
            {
                path: "coin-tracker/:coinId",
                element: <Coin />,
                children: [
                    {
                        path: "price",
                        element: <Price />
                    },
                    {
                        path: "chart",
                        element: <Chart />
                    }
                ]
            }
        ],
        //  path  component  component    
        errorElement: <NotFound />
    }
])

//  index.tsx  <RouterProvider router={router}/>  
export default router;